import React, { Component } from 'react';
import Navbar from './navbar';
import Counters from './counter';
class App extends Component {
	state = {
		counters: [
			{ id: 0, value: 4 },
			{ id: 1, value: 3 },
			{ id: 2, value: 0 },
			{ id: 3, value: 0 }
		]
	};
	constructor(props) {
		super(props);
		console.log('app-constuctor');
	}

	handleDelete = i => {
		const c = this.state.counters.filter(el => el.id !== i);
		this.setState({ counters: c });
	};
	handleReset = () => {
		const reset = this.state.counters.map(c => {
			c.value = 0;
			return c;
		});
		this.setState({ counters: reset });
	};
	handleIncrement = i => {
		const counters = this.state.counters.slice();
		counters[i].value++;
		this.setState({ counters });
	};
	render() {
		return (
			<React.Fragment>
				<Navbar
					totCounters={this.state.counters.filter(c => c.value > 0).length}
				/>
				<main className='container'>
					<Counters
						counters={this.state.counters}
						onReset={this.handleReset}
						onIncrement={this.handleIncrement}
						onDelete={this.handleDelete}
					/>
				</main>
			</React.Fragment>
		);
	}
}

export default App;



